.is-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%h;
    min-height: 10rem;
    background-color: #2c3e50;
  
    .cube {
      width: 2.5rem;
      height: 2.5rem;
      background-color: #e74c3c;
      animation: foldCube 2.4s infinite both;
      transform-origin: 100% 100%;
  
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: #3498db;
        animation: foldCube 2.4s infinite both;
        transform-origin: 0 100%;
      }
    }
  }